<template>
  <div>
    <v-divider></v-divider>
    <v-text-field
      class="pb-5"
      v-model="search"
      append-icon="mdi-magnify"
      label="Search"
      single-line
      hide-details
    ></v-text-field>
    <v-spacer></v-spacer>
    <v-list class="schedule-list">
      <v-list-item v-for="schedule in scheduleList" :key="schedule.name">
        <v-list-item-content class="text-left">
          <v-list-item-title class="text-left" v-text="schedule.name"></v-list-item-title>

          <v-list-item-subtitle class="text-left" v-text="schedule.updated_on"></v-list-item-subtitle>
        </v-list-item-content>

        <v-list-item-action>
          <v-btn :loading="schedule.isDeleting" icon @click="openDeleteDialog(schedule)">
            <v-icon color="red lighten-1">mdi-delete</v-icon>
          </v-btn>
        </v-list-item-action>
      </v-list-item>
    </v-list>
    <confirm-dialog
      :is-open="isOpen"
      :on-close="onCloseDeleteDialog"
      :on-confirm="onRemoveSchedule"
      dialog-text="Are you sure to delete the schedule?"
    ></confirm-dialog>
  </div>
</template>
<script>
import ConfirmDialog from '../Common/ConfirmDialog';

export default {
  name: 'schedule-list',
  props: ['schedules', 'removeSchedule'],
  components: { ConfirmDialog },
  data() {
    return {
      search: '',
      selectedItem: null,
      isOpen: false,
    };
  },
  computed: {
    scheduleList() {
      return this.schedules.filter(val => val && val.name.toLowerCase().indexOf(this.search.toLowerCase()) > -1);
    },
  },
  methods: {
    openDeleteDialog(item) {
      this.selectedItem = item;
      this.isOpen = true;
    },
    onRemoveSchedule() {
      this.removeSchedule(this.selectedItem);
      this.onCloseDeleteDialog();
    },
    onCloseDeleteDialog() {
      this.selectedItem = null;
      this.isOpen = false;
    },
  },
};
</script>
<style>
.schedule-list {
  max-height: calc(100vh - 300px);
  overflow: auto;
  padding-top: 20px;
}
</style>
