<template>
  <div ref="schedulePageElem">
    <v-row>
      <v-col cols="12" sm="12" md="10" lg="6">
        <v-card :loading="isFetchingData">
          <v-card-title>Schedules</v-card-title>
          <v-card-text>
            <v-form v-model="form.valid" ref="form" lazy-validation>
              <v-row>
                <v-col cols="8">
                  <v-text-field
                    v-model="form.scheduleName"
                    label="Schedule Name"
                    :rules="[rules.required]"
                    :error-messages="form.scheduleErrorMessages"
                    required
                  ></v-text-field>
                </v-col>
                <v-col cols="4" class="schedule-page-add-btn">
                  <rbx-button
                    :block="true"
                    :loading="form.isCreatingSchedule"
                    color="pink"
                    :click="createSchedule"
                    title="Add Schedule"
                  ></rbx-button>
                </v-col>
              </v-row>
            </v-form>
            <schedule-list
              v-if="schedules.length > 0"
              :schedules="schedules"
              :remove-schedule="removeSchedule"
            ></schedule-list>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </div>
</template>
<script>
import ScheduleList from './ScheduleList';
import RbxButton from '../Common/RbxButton';
import PageTitle from '../Common/PageTitle.vue';
import * as scheduleService from '../../services/scheduleService';

export default {
  name: 'schedule-page',
  components: { PageTitle, ScheduleList, RbxButton },
  data() {
    return {
      isFetchingData: false,
      schedules: [],
      form: {
        valid: false,
        isCreatingSchedule: false,
        scheduleErrorMessages: [],
      },
      rules: { required: value => !!value || 'Required.' },
    };
  },
  mounted() {
    this.fetchSchedules();
  },
  methods: {
    fetchSchedules(callback) {
      this.isFetchingData = true;
      scheduleService
        .fetch()
        .then(data => {
          this.schedules = data.map(data => ({ ...data, isDeleting: false }));
          typeof callback === 'function' && callback();
        })
        .catch(err => {
          this.$toasted.show('Error occured while fetching schedules', { type: 'error' });
        })
        .finally(() => {
          this.isFetchingData = false;
        });
    },
    createSchedule(schedule) {
      if (!this.$refs.form.validate()) {
        return;
      }
      this.form.isCreatingSchedule = true;
      const payload = {
        name: this.form.scheduleName,
      };
      scheduleService
        .create(payload)
        .then(data => {
          this.$toasted.show('Successfully created schedule');
          this.fetchSchedules();
          this.$refs.form.reset();
        })
        .catch(err => {
          if (err && err.message) {
            this.form.scheduleErrorMessages = err.message;
          }
        })
        .finally(() => {
          this.form.isCreatingSchedule = false;
        });
    },
    removeSchedule(schedule) {
      schedule.isDeleting = true;
      scheduleService
        .removeByName(schedule.name)
        .then(() => {
          this.fetchSchedules(() => {
            this.$toasted.show('Successfully deleted schedule');
          });
        })
        .catch(err => {
          schedule.isDeleting = false;
        });
    },
  },
};
</script>
<style>
.schedule-page-add-btn {
  display: flex;
  align-items: center;
}
</style>
